<template>
  <Container title="Add gallery">
    <form>
      <div class="row">
        <div class="col-md-12">
        <alert :alert="alert" />
          <fg-input 
            type="text"
            label="Choose Location"
            placeholder="Find location"
            @changeValue="handleFindLocation"
            name="parent_id"
            :value="locationName"
          >
            <cascader
              name="location"
              showedKey="title"
              :options="searchResult"
              @handleChoose="handleChoose"
            />
          </fg-input>
          <hr>
          <Gallery
            v-if="locationName"
            title="Add images"
            uploading-text="Uploading..."
            @uploadImage="uploadImage"
            @removeImage="removeImage"
            :gallery="getGallery"
            :parentId="parentId"
          />
        </div>
      </div>
    </form>
  </Container>
</template>

<script>
  import { mapActions, mapGetters, mapState, mapMutations } from 'vuex'

  export default {
    components: {
      ButtonBlock: () => import('ui/Container/ButtonBlock'),
      Container: () => import('ui/Container/Container'),
      Gallery: () => import('ui/Gallery/Gallery')
    },
    data: () =>
      ({
        parentId: '',
        locationName: ''
      }),
    computed: {
      ...mapState({
        alert: state => state.alert
      }),
      ...mapGetters([
        'getGallery',
        'searchResult'
      ])
    },
    methods: {
      handleFindLocation(value) {
        this.locationName = value
        this.findLocation(value)
      },
      handleChoose(value, key) {
        this.parentId = value.location_id
        this.locationName = value.title
        this.addExistingImage(value.galleries)
        this.setSearchResult([])
      },
      ...mapMutations([
        'setSearchResult',
        'addExistingImage'
      ]),
      ...mapActions([
        'uploadImage',
        'removeImage',
        'findLocation'
      ])
    }
  }
</script>

<style>
  .label-role {
    display: block;
  }
</style>



